/*
 *  Featured Content 1
 *    Styles for the featured content 1 section.
 *
 *  Version:
 *    1.0.0 - 2021/10/19
 */

.c-featured-content-1 {
  padding: #{'min(23.46%, 7.9125rem)'} $s-gutter--edge-mobile 5.3125rem;
  background-color: $s-color--background-impact;
  color: $s-color--font-invert;

  &__inner {
    max-width: 31.25rem;
    margin: 0 auto;
  }

  // Images
  &__images {
    position: relative;
    padding-bottom: 9.56%;
    margin-bottom: 2.875rem;
  }

  // Image 1
  &__image-1 {
    &-wrapper {
      position: relative;
      width: 82.09%;
      margin-left: auto;
    }

    &-container {
      position: relative;
      padding-bottom: 92%;
      display: block;

      &.no-pointer{
        cursor: default;
      }
    }
  }

  &__illustration-1 {
    &-wrapper {
      position: absolute;
      top: -25.26%;
      left: -9.64%;
      width: 67.5%;
    }

    &-container {
      padding-bottom: 93.02%;
    }
  }

  &__illustration-2 {
    &-wrapper {
      position: absolute;
      top: -28.46%;
      left: -21.09%;
      width: 38.55%;
      z-index: $s-z--text-overlay;
    }

    &-container {
      padding-bottom: 87.29%;
      transform: scaleX(-1);
    }
  }

  &__illustration-3 {
    &-wrapper {
      position: absolute;
      bottom: -11.86%;
      right: 2.9%;
      width: 81.81%;
    }

    &-container {
      padding-bottom: 64.73%;
    }
  }

  // Image 2
  &__image-2 {
    &-wrapper {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 38.21%;
    }

    &-container{
      display: block;

      &.no-pointer{
        cursor: default;
      }
    }
  }

  // Title
  &__title {
    margin-bottom: 1.4375rem;
  }

  // Content
  &__content {
    margin-bottom: 1rem;

    &-container {
      padding: 0 0.875rem 0 2.625rem;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {
    padding: 16% 2.8125rem 5.3125rem;

    &__inner {
      max-width: none;
    }

    &__columns {
      display: flex;
      justify-content: space-between;
      flex-direction: row-reverse;
    }

    &__column {
      &.is-image-column {
        width: 55%;
        padding-left: 1rem;
      }

      &.is-content-column {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 45%;
        padding-right: 1rem;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    padding: #{'min(6%, 7.5rem)'} #{'min(6%, 7.5rem)'} 5.875rem;

    &__inner {
      max-width: 110rem;
      margin: 0 auto;
    }

    &__column {
      &.is-image-column {
        width: 61.65%;
        padding-left: #{'min(5.24%, 2.1875rem)'};
      }

      &.is-content-column {
        width: 38.35%;
        padding-right: #{'min(5.24%, 2.1875rem)'};
      }
    }

    &__illustration-1-wrapper {
      top: -5.566%;
      left: -18.64%;
      width: 49%;
    }

    &__illustration-2-wrapper {
      top: -3.36%;
      left: -33.89%;
      width: 29.55%;
    }

    &__illustration-3-wrapper {
      bottom: -10.26%;
      right: 5.9%;
      width: 51.31%;
    }

    &__title {
      margin-bottom: 2.625rem;
    }

    &__content {
      margin-bottom: 2.5rem;

      &-container {
        padding: 0 2.375rem 0 4.125rem;
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__column {
      &.is-content-column {
        transform: translateY(-1.1%);
      }
    }

    &__title {
      margin-bottom: 4.4375rem;
    }

    &__content {
      margin-bottom: 4.125rem;

      &-container {
        padding: 0 5.375rem 0 6.875rem;
      }
    }

    &__images {
      margin-bottom: 0;
    }
  }
}
